*,
*::before,
*::after {
  box-sizing: border-box;
}

.App {
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.quizzikle-head{
  // font-size: 2vh;
  font-size: clamp(24px, 4vh, 18px);
  text-align: center;
}

.main-container{

  background-color: cyan;
  // height: 600px;
  // padding: 20px;
  padding-bottom:40px ;
  width: 80%;
  margin: 0 auto;
  border-radius: 20px;
}

.question{
  font-size: 4vh;
  font-style:bold;
  padding : 10px 10px;
  
  // margin-top: 40px;
}

.options-container{
  font-size: 20px;
  list-style-type: none;
  text-align: left;
  margin: 0 auto;
  // padding-left: 20px;
  max-width: 95%;
 
}

.options{
  display: flex;
  // justify-content: left;
  align-items: center;
  border: 1px solid black;
  // margin-top: 5px;
  margin: 5px auto;
  // background-color: red;
  // padding: 5px;
  font-size:4vh;
  border-radius: 10px;
  height: 40px;
  padding-left: 0px;
}

.incorrect-option{
  background-color: red;
}


.correct-option{
  background-color: darkgreen;
}

.option{
  padding-left: 5px;
}

.tick-untick{
  width: 30px;
  height: 30px;
}

.submit-btn{
  width: 95%;
  max-width: 100%;
  margin: 0 auto;
  height: 40px;
  margin-top: 10px;
  border-radius: 10px;
  color: white;
  background-color: green;
}

.submit-btn-text{
  font-size: 3vh;
  font-style: bold;
  ;
}


.pop-message{
  font-weight: 800;
  font-style: italic;
}

@media (max-width: 480px) {
  .option {
    font-size: 14px;              /* minimum font size */
    padding: 0.4rem 0.8rem;
  }
  .question{
    font-size: 20px;
  }
  
  .quizzikle-head{
    font-size: 24px;
  }
  .tick-untick {
    width: 8%;                     /* slightly larger relative to smaller screen */
  }
}